import http from '../interface'

// 
export const login = (data) => {
    return http.request({
        url: 'user/auth',
        method: 'POST', 
        data,
		// handle:true
    })
}
// token
export const ValidationToken = (data) => {
    return http.request({
        url: 'life/validationToken',
        method: 'POST', 
    })
}

// 
export const getInformation = (data) => {
    return http.request({
        url: 'user/information',
        method: 'get', 
    })
}


export default {
	login,
	ValidationToken,
	getInformation
}